require("angular");
require('angular-ui-router/release/angular-ui-router');
require('ocLazyLoad/dist/ocLazyLoad');
var app = angular.module('app', [
    'ui-router',
    'oc.lazyLoad'
]);
app.config(['$stateProvider', '$locationProvider', '$urlRouterProvider', function ($stateProvider, $urlRouterProvider, $locationProvider) {
        alert(1);
        $stateProvider.state('home', {
            url: '/home',
            templateProvider: function ($q) {
                var deferred = $q.defer();
                require.ensure(['./module/home/home.html'], function (require) {
                    var template = require('./module/home/home.html');
                    deferred.resolve(template);
                }, 'home-tpl');
                return deferred.promise;
            },
            controller: 'homeCtrl',
            controllerAs: 'vm',
            resolve: {
                'app.home': function ($q, $ocLazyLoad) {
                    var deferred = $q.defer();
                    require.ensure(['./module/home/home.controller'], function () {
                        var mod = require('./module/home/home.controller');
                        $ocLazyLoad.load({
                            name: 'app.home'
                        });
                        deferred.resolve(mod.controller);
                    }, 'home-ctl');
                    return deferred.promise;
                }
            }
        });
        $locationProvider.html5Mode(true);
        $urlRouterProvider.otherwise("/home");
    }]);
console.log(app.config);



